        padding: 10px;
        margin-top: 150px;
        margin-bottom: 100px;
        border-radius: 4px;
        
        
        background: rgba(122, 80, 80, 0.2);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
